<template>
  <div
    v-if="visible"
    class="popUp"
  >
    <div class="d-flex flex-column">
      <slot />
    </div>
  </div>
</template>

<script>

export default {
  name: 'popUp',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    window.addEventListener("click", function(event) {
      if(!event.target.dataset.popup) {
        this.$emit('close')
      }
    }.bind(this));
  },
}
</script>

